/* Replacing Text Animation */
.slider-wrapper {
  font-size: 36px;
  color:#000;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}
.text-slider{
  height: 50px;
  padding-left:2px;
  padding-right:2px;
  overflow: hidden;
}
.text-slider div {
  color:#000;
  height: 50px;
  margin-bottom: 50px;
  padding: 2px 15px;
  text-align: center;
  box-sizing: border-box;
}
.slider-up {
  animation: slideUp 10s linear infinite;
  }
.slider-down {
  animation: slideDown 10s linear infinite;
}
@keyframes slideDown {
  0% {margin-top:-300px;}
  5% {margin-top:-200px;}
  33% {margin-top:-200px;}
  38% {margin-top:-100px;}
  66% {margin-top:-100px;}
  71% {margin-top:0px;}
  100% {margin-top:0px;}
}
@keyframes slideUp {
  0% {margin-top:100px;}
  5% {margin-top:0px;}
  33% {margin-top:0px;}
  38% {margin-top:-100px;}
  66% {margin-top:-100px;}
  71% {margin-top:-200px;}
  100% {margin-top:-200px;}
}
/* Replacing Text Animation */

